/* -----------------------------------------   NAV   ----------------------------------------- */


nav {
  background-color: rgba(101, 69, 31, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid #65451F;
  padding: 10px 0px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: auto;
  height: 3.5vh;
  margin: 20px 0px;
  margin-left: 20px;
  object-fit: cover;
}

nav .center {
  display: flex;
}

nav .center a {
  color: #100B05;
  text-decoration: none;
  padding: 10px;
  margin-right: 20px;
  font-size: min(1.1rem);
}

nav .right {
  display: flex;
  align-items: center;
}

nav .right a,
.dropdown-icon ,nav .right span{
  color: #100B05;
  text-decoration: none;
  padding: 10px;
  margin-right: 20px;
  font-size: min(1rem);
  background: none;
  border: none;
  cursor: pointer;
}

.dropdown-icon{
  display: none;
}

.dropdown-icon i{
  color: #100B05;
    text-decoration: none;
    font-size: min(1rem);
    background: none;
    border: none;
    cursor: pointer;
    margin-left: 20px;
}

.right sup{
  background-color: rgb(168, 2, 2);
  color: white;
  border-radius: 50%;
  height:15px;
  width: 15px;
  text-align: center;
  padding-bottom: 2px;
  display: inline-block;
}

nav .dropdown-menu {
display: hidden;
pointer-events: none;
  /* flex-direction: column; */
  position: absolute;
  top: 90px;
  /* right: 1px; */
  left: 1px;
  z-index: 4;
  background-color: rgb(173, 148, 120);
  border: 1px solid #65451F;
  padding: 10px ;
  border-radius: 5px;
  width: 200px;
  height: 86%;
  text-align: left;
  color: black;
  transform: translateX(-100%);
    /* Start slightly above its final position */
    opacity: 0;
    /* Initially transparent */
    /* Initially hidden */
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s;
}

nav .subdropdown{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
  
}

nav .uppermenu , nav .lowermenu{
  display: flex;
    flex-direction: column;
    gap:20px;
}

.uppermenu a i{
  margin-right:10px ;
}

nav .dropdown-menu a {
  text-decoration: none;
  font-size: medium;
  /* color: rgb(63, 40, 11); */
  color: black;
  font-weight:500;
}

nav .dropdown-menu.open {
  opacity: 1;
  transform: translateX(0);
    /* Slide into place */
    visibility: visible;
    pointer-events: all;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.433);
  /* Semi-transparent black */
  z-index: 3;
  /* Ensure it is below the dropdown menu */
  display: none;
  /* Initially hidden */
  transition: opacity 0.3s ease;
  /* Smooth transition for fading */
}

.overlay.open {
  display: block;
  /* Show overlay when open */
  opacity: 1;
}

.dropdown-menu {
  z-index: 20;
  /* Ensure it is above the overlay */
  /* Add any existing styles for dropdown */
}

@media (max-width: 900px) {
  nav .center {
    display: none;
  }
  /* nav .logo img{
    width: 40%;
  } */
     nav .logo img {
       height: 3vh;
       width: 100%;
     }

    .dropdown-icon {
    display: block;
  }
    nav .right a,
    .dropdown-icon,
    nav .right span{
