/* -----------------------------SUBMENU------------------------------ */

.menu-icon {
  width: 28px;
  margin-left: 20px;
  display: none;
}

.menu-list{
  display: flex;
  justify-content: space-evenly;
  margin: 30px 0px;
  font-size: 1.2rem;
  font-size: min(22px);
  color: black;
  background-color: #f5f5f5;
}

.menu-list a{
  text-decoration: none;
    color: #333;
    /* font-size: 1px; */
    padding: 10px 20px;
      position: relative;
      transition: color 0.3s;
}



.menu-list a::after {
  content: '';
    position: absolute;
    width: 0;
    height: 2px;
    display: block;
    margin-top: 7px;
    /* right: 0; */
    background: #65451fd1;
    transition: width 0.3s ease;
    /* -webkit-transition: width 0.3s ease; */
}
.menu-list a:hover::after {
  width: 100%;
    left: 0;
    background: #65451fd1;
}
.menu-list a.active {
  border-bottom:2px solid #65451fd1;
  color: #65451fd1;
}
.menu-list a.active::after {
  content: none;
  /* Remove underline animation for active link */
}

