@keyframes animateCircle {
  from{
    opacity: 0;
    transform: translateX(-50%);
  }
  to{
    opacity: 1;
    transform: translateX(0%);
  }
}

.circle {
  border: 10px solid white;
  background-color: beige;
  background-image: url('https://i.pinimg.com/564x/1d/62/bd/1d62bd281e4347d8df87b5a4a020188b.jpg');
  background-size: cover;
  min-width: 25rem;
  min-height: 25rem;
  width: 30vw;
  height: 30vw;
  border-radius: 50%;
  position: absolute;
  /* top: 5vw; */
  top:max(10vh);
  left: 8vw;
  float: left;
  shape-outside: circle(50%);
    /* Define a circle shape for the text to wrap around */
    clip-path: circle(50%);
    /* Ensure the circle itself is clipped to a circle */
    animation: animateCircle 0.75s;
}

