/* ------------------------------------ COFFEE  ---------------------------------- */
.coffee {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 2.5vw;
  padding: 2vh 2vw;
  padding-bottom: 180px;
}


.CoffeeItems {
  text-align: center;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  background-color: #d3d8d4;
  /* background-color: #c9cbc9; */
  /* background-color: rgb(211, 225, 237); */
  /* background-color:  rgba(160, 137, 108, 0.556); */
  overflow: hidden;
  padding: 7%;
}

.CoffeeItems>h3 {
  margin-bottom: 10px;
}

.price {
  height: auto;
}

.CoffeeItems:hover {
  box-shadow: 8px 8px 20px 2px rgba(51, 51, 51, 0.865);
}

.pic {
  display: inline-block;
  margin: auto;
  /* margin-top: 5%; */
  height: 11%;
  width: 11%;
  background-color: rgb(118, 88, 39);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pic img {
  border-radius: 50%;
  height: 10vw;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.pic:hover img {
  transform: scale(1.1);
  /* Scale the image to 110% on hover */
}

.coffee h3 {

  text-align: center;
  color: rgb(101, 69, 31);
  font-size: 2.1vw;
  font-size: min(30px);
}

.CoffeeItems p {
  text-align: center;
  font-size: 1.5vw;
  font-size: min(20px);
  font-weight: bold;
  /* margin-right: 20px; */
  color: rgb(101, 69, 31);
  margin: 0px;
}

@media (min-width: 1200px) {
